import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { MovieList, MovieDetails, NewMovie, EditMovie, NotFound } from './pages/index';
import { Header } from './components/index';

import './App.css';
import './components/MovieForm.css';
import './pages/MovieDetails.css';

function App() {
  return (
    <div className="body-html">
      <BrowserRouter>
        <Header />
        <Switch>
          <Route exact path="/Block13-Project_MovieCard_CRUD//movies/new"><NewMovie /></Route>
          <Route exact path="/Block13-Project_MovieCard_CRUD//movies/:id" render={(props) => <MovieDetails {...props} />} />
          <Route exact path="/Block13-Project_MovieCard_CRUD//movies/:id/edit" render={(props) => <EditMovie {...props} />} />
          <Route exact path="/Block13-Project_MovieCard_CRUD/"><MovieList /></Route>
          <Route><NotFound /></Route>
        </Switch>
      </BrowserRouter>
    </div>
  );
}

export default App;
